import { useState } from "react"

function HomePage() {
    const [state, setState] = useState();

    return <div id="home">
        <style jsx>{`
        p {
          color: blue;
        }
        div#home {
          background: red;
          display: flex;
          flex-direction: column;
        }
        @media (max-width: 600px) {
          div {
            background: blue;
          }
        }
      `}</style>

        <form>
        <label>당신의 별명을 입력하새오</label>
        <input type="text" value="state" required />
        <input type="submit" />
        </form>
    </div>
  }
  
  export default HomePage